import React, { useState, useCallback, useRef, useEffect } from 'react';
import { Link, useHistory } from 'umi';
import { Table, Space, Button, Modal, Form, Row, Col, message, Popconfirm } from 'antd';
import { Prompt } from 'react-router-dom';
import { DndProvider, useDrag, useDrop } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';
import update from 'immutability-helper';
import { withAuthorized } from '@/utils/Authorized';
import { Role } from '@/utils/authority';
import {
  getRealTimeDramaContentList,
  getDramaAdjustList,
  batchGetDramaContentById,
  getDramaContentById,
  heatAdjust
} from '../services';
import DramaContentSearch from '@/components/DramaContentSearch';
import ExpTime from '@/components/ExpTime';
import Countdown from '@/components/Countdown';
import PreviewImg from '@/components/PreviewImg';
import moment from 'moment';
import { AdjustListItem, AdjustTableData } from '../types';
import { ResponseData } from '@/types/types';
import { RedoOutlined } from '@ant-design/icons';

const type = 'DragableBodyRow';

interface Props {
  username: string;
}

interface DragableBodyRowType {
  index: number;
  moveRow: any;
  className: any;
  style: any;
  [key: string]: any;
}

interface KeyValue {
  [key: string]: any;
}

const DragableBodyRow = ({ index, moveRow, className, style, ...restProps }: DragableBodyRowType) => {
  const ref = useRef<any>();
  const [{ isOver, dropClassName }, drop] = useDrop({
    accept: type,
    collect: (monitor: any) => {
      const { index: dragIndex } = monitor.getItem() || {};

      if (dragIndex === index) {
        return {};
      }

      return {
        isOver: monitor.isOver(),
        dropClassName: dragIndex < index ? ' drop-over-downward' : ' drop-over-upward',
      };
    },
    drop: (item: any) => {
      moveRow(item.index, index);
    },
  });
  const [, drag] = useDrag({
    type,
    item: { index },
    collect: (monitor: any) => ({
      isDragging: monitor.isDragging(),
    }),
  });

  drop(drag(ref));

  return (
    <tr ref={ ref }
      className={ `${ className }${ isOver ? dropClassName : '' }` }
      style={{ cursor: 'move', ...style }}
      { ...restProps }
    />
  );
};

const HotSearch: React.FC<Props> = (props: Props) => {
  const { username } = props;
  const [loading, setLoading] = useState(false);
  const [realTimeList, setRealTimeList] = useState<any[]>([]); // 实时数据列表
  const [tableData, setTableData] = useState<any[]>([]); // 列表数据
  const [baseTableData, setBaseTableData] = useState<any[]>([]); // 初始页面数据，回复按钮点击时调用
  const [replacedData, setReplacedData] = useState<any>({}); // 替换数据列表
  const [modalVisible, setModalVisible] = useState(false);
  const [rangTimeModalVisible, setRangTimeModalVisible] = useState(false);
  const [modifyRangTimeIndex, setModifyRangTimeIndex] = useState(0);
  const [submitLoading, setSubmitLoading] = useState(false);
  const [haveModified, setHaveModified] = useState(false);
  const [form] = Form.useForm();
  const [rangTimeform] = Form.useForm();
  const leaveLockRef = useRef(false);
  const history = useHistory();

  // 获取移除重复数据的list
  /**
   * 
   * @param replaceList 替换的列表
   * @returns 返回去重且按照当前热度插入替换列表的list
   */
  const getRemoveRepeatList = (replaceList: any[], realTimeDramaList?: any[]) => {
    // 获取实时列表移除当前替换数据后的数据
    const removeRealRepeatList = (realTimeDramaList || realTimeList).filter((r: any) => !(replaceList.find((item: any) => item.dramaId === r.dramaId)) );
    const finalList: any[] = [];
    // 创建长度为12的空数组
    const arr = new Array(12);
    // 记录当前去除替换数据后的真实热度列表数据的索引值
    let currentRealIndex = 0;

    for (let i = 0; i < arr.length; i++) {
      // 判断当前位置是不是展示替换数据
      // 如果是，则该位置数据变为替换数据
      // 如果不是，则从移除替换数据的实时热度数据里面依次取值
      const currentIndexEditData = replaceList.find((r: any) => r.adjustRank === i);

      if (currentIndexEditData) {
        finalList[i] = currentIndexEditData;
      } else {
        finalList[i] = removeRealRepeatList[currentRealIndex];
        currentRealIndex++;
      }
    }
    
    return finalList.map((item: any) => {
      const { adjustRank, ...rest } = item || {};

      return rest;
    });
  };

  const initData = () => {
    setLoading(true);
    // 获取真实热度列表
    const getRealTimeDramaContentListRes = new Promise((resolve: any, reject: any) => {
      getRealTimeDramaContentList()
        .then((res: ResponseData<AdjustTableData[]>) => {
          if (res.code === 200) {
            const { data: list = [] } = res || {};

            resolve(list);
          } else {
            reject();
          }
          
        })
        .catch((err: any) => reject(err));
    });
    
    // 获取调整列表
    const getDramaAdjustListRes = new Promise((resolve: any, reject: any) => {
      getDramaAdjustList()
        .then((res: ResponseData<any>) => {
          const { data, code } = res;

          if (code === 200) {
            resolve(data);
          } else {
            reject();
          }
        })
        .catch((err: any) => reject(err));
    });

    Promise.all([getRealTimeDramaContentListRes, getDramaAdjustListRes])
      .then(([realRes, adjustRes]: any) => {
        setRealTimeList(realRes);
        const { result: adjustListRes = [], operator } = adjustRes || {};
        // 只展示当前在榜的调整作品
        const adjustList = adjustListRes.filter((r: AdjustListItem) => r.onRankTime > moment().valueOf());
        const adjustIds = adjustList.map((item: AdjustListItem) => item.dramaId);

        // 根据调整列表dramaId，查询调整的详情
        if (adjustList.length > 0) {
          batchGetDramaContentById(adjustIds)
            .then((res: ResponseData<KeyValue>) => {
              const { data: adjustDramsDetails = {}, code } = res;

              if (code === 200) {
                // 获取调整数据，合并调整列表和详情数据
                const mergeAdjustList = adjustList.map((item: AdjustListItem) => {
                  const { dramaId, adjustRank, } = item; // adjustRank从1开始
              
                  return ({ ...item, ...(adjustDramsDetails[dramaId]), replaced: true, operator, adjustRank: adjustRank - 1 });
                });
                // 获取合并数据
                const finalList = getRemoveRepeatList(mergeAdjustList, realRes);

                // 判断当前列表是否有下线作品
                if (finalList.some((r: any) => r.replaced && r.onlineStatus === 0)) {
                  Modal.info({
                    title: '下线作品请移除！'
                  });
                }
            
                setTableData(finalList);
                setBaseTableData(finalList);
                setLoading(false);
                setHaveModified(false);
                leaveLockRef.current = false;
              }
            })
            .catch(() => {
              setSubmitLoading(false);
            });
        } else {
          setTableData(realRes);
          setBaseTableData(realRes);
          setLoading(false);
          setHaveModified(false);
          leaveLockRef.current = false;
        }
      })
      .catch(() => {
        setLoading(false);
      });
  };

  useEffect(() => {
    initData();
  }, []);

  // 刷新按钮点击
  const reloadHandle = () => {
    if (haveModified) {
      Modal.confirm({
        title: '提示',
        content: '如果刷新，当前未提交的数据会被忽略，请问是否继续操作？',
        onOk: () => {
          initData();
        }
      });
    } else {
      initData();
    }
  };
  

  // 替换
  const replaceHandle = (record: any, index: number) => {
    setModalVisible(true);
    setReplacedData({ ...record, index });
  };

  // 移除
  const deleteHandle = (index: number) => {
    const copyTableData = [...tableData];

    // copyTableData.splice(index, 1);
    const replaceList = copyTableData.reduce((preArr: any, currentVal: any, i: number) => {
      if (currentVal.replaced && i !== index) {
        return [...preArr, { ...currentVal, adjustRank: i }];
      } else {
        return preArr;
      }
    }, []);
    const finalList: any[] = getRemoveRepeatList(replaceList);

    setTableData(finalList);
    setHaveModified(true);
    leaveLockRef.current = true;
  };
  
  // 修改在榜截止时间
  const changeRangTime = (index: number) => {
    setRangTimeModalVisible(true);
    setModifyRangTimeIndex(index);
  };

  const columns = [
    {
      title: (
        <React.Fragment>
          热搜位排名<br />(支持拖动排序)
        </React.Fragment>
      ),
      width: 130,
      render: (text: string, record: AdjustTableData, index: number) => index + 1
    },
    {
      title: '名称',
      dataIndex: 'title',
      key: 'title',
      render: (text: string, record: AdjustTableData) => <Link to={ `/content-manage/production-manage/${ record.dramaId }` }>{text}</Link>
    },
    {
      title: (
        <React.Fragment>
          海报<br />(缩略图)
        </React.Fragment>
      ),
      width: 130,
      dataIndex: 'coverImage',
      key: 'coverImage',
      render: (text: string) => {
        if (text) {
          const coverImageList = JSON.parse(text);

          if (coverImageList.length > 0) {
            return <PreviewImg imgUrls={ coverImageList[0] } placement="right" />;
          }
        }

        return '';
      }
    },
    {
      title: '搜索热度',
      dataIndex: 'searchHeat',
      key: 'searchHeat',
    },
    {
      title: '最新操作人',
      dataIndex: 'operator',
      key: 'operator',
    },
    {
      title: '作品状态',
      dataIndex: 'onlineStatus',
      key: 'onlineStatus',
      render: (text: number) => {
        return (text === 0 ? <span style={{ color: 'rgb(248, 29, 34)' }}>下线</span> : '上线');
      }
    },
    {
      title: (
        <React.Fragment>
          剩余在榜时间<br />(点击操作)
        </React.Fragment>
      ),
      dataIndex: 'onRankTime',
      key: 'onRankTime',
      render: (text: string, record: AdjustTableData, index: number) => text && (
        <div onClick={ () => changeRangTime(index) }>
          <Countdown time={ text } overTimeText="已过期" />
        </div>
      )
    },
    {
      title: '管理',
      dataIndex: '',
      key: 'operate',
      width: 140,
      render: (text: string, record: AdjustTableData, index: number) => {
        return (
          <Space>
            <Button type="primary" size="small" onClick={ () => replaceHandle(record, index) }>替换</Button>
            {record.replaced && (
              <Popconfirm title="确定要移除该替换数据吗？"
                onConfirm={ () => deleteHandle(index) }
              >
                <Button danger={ true } size="small">移除</Button>
              </Popconfirm>)}
          </Space>
        );
      }
    },
  ];

  const components = {
    body: {
      row: DragableBodyRow,
    },
  };

  // 拖拽后触发
  const moveRow = useCallback(
    /**
     * 
     * @param dragIndex 开始拖拽的位置
     * @param hoverIndex 拖拽后的位置
     */
    (dragIndex: number, hoverIndex: number) => {
      const dragRow = tableData[dragIndex];

      setTableData(
        update(tableData, {
          $splice: [
            [dragIndex, 1],
            [hoverIndex, 0, dragRow],
          ],
        }),
      );
      setHaveModified(true);
      leaveLockRef.current = true;
    },
    [tableData],
  );

  // 替换热搜位弹窗 确认
  const modalOkHandle = () => {
    form.validateFields().then((values: KeyValue) => {
      const { dramaInfo, onRankTime } = values;
      const { label, value } = dramaInfo;
      const { index } = replacedData;

      // 替换数据
      const formatData: AdjustTableData = {
        title: label,
        dramaId: value,
        replaced: true,
        onRankTime,
        operator: username
      };

      // 判断是否当前作品已经被替换
      if (tableData.find((r: AdjustTableData) => r.dramaId === value && r.replaced)) {
        message.warn('当前作品已经被替换过，请核验！');

        return;
      }
      getDramaContentById(value).then((res: AdjustTableData) => {
        if (res.code === 200) {
          const { coverImage, searchHeat } = res?.data || {};

          formatData.coverImage = coverImage;
          formatData.searchHeat = searchHeat;
          const copyTableData = [...tableData];

          copyTableData[index] = formatData;
          // 获取替换列表
          const replaceList = copyTableData.reduce((preArr: any, currentVal: AdjustTableData, i: number) => {
            if (currentVal.replaced) {
              return [...preArr, { ...currentVal, adjustRank: i }];
            } else {
              return preArr;
            }
          }, []);
        
          // 最终table展示数据
          const finalList: AdjustTableData[] = getRemoveRepeatList(replaceList);

          setTableData(finalList);
          setModalVisible(false);
          form.resetFields();
          setHaveModified(true);
          leaveLockRef.current = true;
        }
      });
    });
  };
  // 替换热搜位弹窗 取消
  const modalCancelHandle = () => {
    setModalVisible(false);
    form.resetFields();
  };
  
  // 确定修改按钮事件
  const modifyConfirmhandle = () => {
    setSubmitLoading(true);
    if (tableData.some((r: any) => r.replaced && r.onlineStatus === 0)) {
      setSubmitLoading(false);
      Modal.warn({
        title: '当前替换数据中存在下线作品，请移除或替换新的作品后再提交！'
      });

      return;
    }
    const sendParams = tableData.reduce((preArr: any[], current: AdjustTableData, index: number) => {
      if (current.replaced) {
        const { dramaId, onRankTime } = current;

        return [...preArr, { dramaId, onRankTime: moment(onRankTime).valueOf(), adjustRank: index + 1 }];
      }

      return preArr;
    }, []);

    const nowTimeValue = moment().valueOf();
    const existOverdue = sendParams.some((r: AdjustListItem) => nowTimeValue >= r.onRankTime); // 存在过期数据

    if (existOverdue) {
      setSubmitLoading(false);
      Modal.warn({
        title: '当前替换数据中存在过期数据，请移除后再提交！'
      });

      return;
    }

    heatAdjust(sendParams)
      .then((res: ResponseData<any>) => {
        if (res.code === 200) {
          setSubmitLoading(false);
          Modal.success({
            title: '修改成功！',
            onOk: () => {
              initData();
            }
          });
        } else {
          setSubmitLoading(false);
          setHaveModified(false);
          leaveLockRef.current = false;
        }
      })
      .catch(() => {
        setSubmitLoading(false);
      });
  };

  const rangTimeModalOkHandle = () => {
    rangTimeform.validateFields().then((values: KeyValue) => {
      const copyTableData = [...tableData];

      copyTableData[modifyRangTimeIndex]['onRankTime'] = values.onRankTime;

      setTableData(copyTableData);
      message.success('修改成功！');
      form.resetFields();
      setRangTimeModalVisible(false);
      setHaveModified(true);
      leaveLockRef.current = true;
    });
  };

  const rangTimeModalCancelHandle = () => {
    form.resetFields();
    setRangTimeModalVisible(false);
  };
  
  return (
    <React.Fragment>
      <Prompt when={ haveModified }
        message={ (location: any) => {
          if (!leaveLockRef.current) {
            return true;
          }
          Modal.confirm({
            title: '即将离开页面',
            content: '当前正在编辑的热门搜索榜列表数据还未提交, 确认离开吗？',
            onOk: () => {
              leaveLockRef.current = false;
              setHaveModified(false);
              history.push(location.pathname);
            },
          });

          return !leaveLockRef.current;
        } }
      />
      <Row justify="end">
        <Col>
          <Button type="primary" icon={ <RedoOutlined /> } style={{ marginBottom: '20px' }} onClick={ reloadHandle }>刷新</Button>
        </Col>
      </Row>
      <div style={{ paddingBottom: '72px' }}>
        <DndProvider backend={ HTML5Backend }>
          <Table columns={ columns }
            loading={ loading }
            dataSource={ tableData }
            components={ components }
            rowKey="dramaId"
            pagination={ false }
            bordered={ true }
            onRow={ (record: any, index: number) => ({
              index,
              moveRow,
            }) }
          />
        </DndProvider>
      </div>
      <Row justify="center"
        style={{
          backgroundColor: '#fff',
          position: 'fixed',
          bottom: '0',
          right: '24px',
          width: 'calc(100% - 256px)',
          padding: '20px 0',
          borderTop: '1px solid #ccc'
        }}>
        <Col>
          <Popconfirm title="确定要忽略本次数据变更吗？"
            onConfirm={ () => {
              setTableData(baseTableData);
              setHaveModified(false);
              leaveLockRef.current = false;
            } }
          >
            <Button>恢复</Button>
          </Popconfirm>
        </Col>
        <Col offset={ 2 }>
          <Button type="primary" onClick={ modifyConfirmhandle } loading={ submitLoading }>确定修改</Button>
        </Col>
      </Row>
      <Modal title="替换热搜位"
        visible={ modalVisible }
        onOk={ modalOkHandle }
        onCancel={ modalCancelHandle }
      >
        <Form form={ form } labelCol={{ span: 4 }}>
          <Form.Item label="选择作品" name="dramaInfo" rules={ [{ required: true }] }>
            <DramaContentSearch />
          </Form.Item>
          <Form.Item label="截至到" name="onRankTime">
            <ExpTime showTimeFormat="HH:mm:ss" />
          </Form.Item>
        </Form>
      </Modal>

      <Modal title="在榜时间选择"
        visible={ rangTimeModalVisible }
        onOk={ rangTimeModalOkHandle }
        onCancel={ rangTimeModalCancelHandle }
      >
        <Form form={ rangTimeform } labelCol={{ span: 4 }}>
          <Form.Item label="截至到" name="onRankTime" rules={ [{ required: true, message: '请选择在榜截止时间' }] }>
            <ExpTime showTimeFormat="HH:mm:ss" />
          </Form.Item>
        </Form>
      </Modal>
    </React.Fragment>
  );
};

export default withAuthorized(HotSearch, [Role.SYSTEM]);
// export default HotSearch;
